import React from "react";
import { FaAngleLeft, FaAngleRight } from "react-icons/fa6";

function Pagination({ currentPage, totalPages, onPageChange }) {
  const handleClick = (page) => {
    return () => {
      if (page > 0 && page <= totalPages) {
        onPageChange(page);
      }
    };
  };

  const renderPages = () => {
    const maxVisiblePages = 5; // S lng nt s ti a hin th

    let pages = [];

    // Nu totalPages nh hn hoc bng maxVisiblePages, hin th tt c cc trang
    if (totalPages <= maxVisiblePages) {
      for (let i = 1; i <= totalPages; i++) {
        pages.push(
          <button
            key={i}
            onClick={handleClick(i)}
            disabled={i === currentPage}
            className={`p-4 mx-3 hover:text-white ${
              i === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            {i}
          </button>
        );
      }
    } else {
      // Nu totalPages ln hn maxVisiblePages, ch hin th mt phn cc trang v du ...
      if (currentPage <= 4) {
        // Hin th cc trang t 1 n 3 v du ...
        for (let i = 1; i <= 4; i++) {
          pages.push(
            <button
              key={i}
              onClick={handleClick(i)}
              disabled={i === currentPage}
              className={`p-2 md:p-4 mx-1 hover:text-white md:mx-3 ${
                i === currentPage ? "text-blue-500" : "text-gray-700"
              }`}
            >
              {i}
            </button>
          );
        }
        pages.push(
          <span
            key="ellipsis"
            className="mx-1 md:mx-3 flex justify-center items-center text-gray-700"
          >
            ...
          </span>
        );
        pages.push(
          <button
            key={totalPages - 1}
            onClick={handleClick(totalPages - 1)}
            className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
              totalPages - 1 === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            {totalPages - 1}
          </button>
        );
        pages.push(
          <button
            key={totalPages}
            onClick={handleClick(totalPages)}
            className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
              totalPages === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            {totalPages}
          </button>
        );
      } else if (currentPage >= totalPages - 2) {
        // Hin th du ... v cc trang t totalPages - 3 n totalPages
        pages.push(
          <button
            key={1}
            onClick={handleClick(1)}
            className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
              1 === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            1
          </button>
        );
        pages.push(
          <button
            key={2}
            onClick={handleClick(2)}
            className={`p-2 md:p-4 mx-1 hover:text-white md:mx-3 ${
              2 === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            2
          </button>
        );
        pages.push(
          <span
            key="ellipsis"
            className="mx-1 md:mx-3 flex justify-center items-center text-gray-700"
          >
            ...
          </span>
        );
        for (let i = totalPages - 3; i <= totalPages; i++) {
          pages.push(
            <button
              key={i}
              onClick={handleClick(i)}
              disabled={i === currentPage}
              className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
                i === currentPage ? "text-blue-500" : "text-gray-700"
              }`}
            >
              {i}
            </button>
          );
        }
      } else {
        // Hin th du ... v cc trang gn currentPage
        pages.push(
          <button
            key={1}
            onClick={handleClick(1)}
            className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
              1 === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            1
          </button>
        );
        pages.push(
          <span
            key="ellipsis"
            className="mx-1 md:mx-3 flex justify-center items-center text-gray-700"
          >
            ...
          </span>
        );
        for (let i = currentPage - 1; i <= currentPage + 1; i++) {
          pages.push(
            <button
              key={i}
              onClick={handleClick(i)}
              disabled={i === currentPage}
              className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
                i === currentPage ? "text-blue-500" : "text-gray-700"
              }`}
            >
              {i}
            </button>
          );
        }
        pages.push(
          <span
            key="ellipsis2"
            className="mx-1 md:mx-3 flex justify-center items-center text-gray-700"
          >
            ...
          </span>
        );
        pages.push(
          <button
            key={totalPages}
            onClick={handleClick(totalPages)}
            className={`p-2 md:p-4 mx-1 md:mx-3 hover:text-white ${
              totalPages === currentPage ? "text-blue-500" : "text-gray-700"
            }`}
          >
            {totalPages}
          </button>
        );
      }
    }

    return pages;
  };

  return (
    <div className="mt-12 flex justify-center">
      <button
        onClick={handleClick(currentPage - 1)}
        disabled={currentPage === 1}
        className="p-4 opacity-70 hover:opacity-100"
      >
        <FaAngleLeft />
      </button>
      {renderPages()}
      <button
        onClick={handleClick(currentPage + 1)}
        disabled={currentPage === totalPages}
        className="p-4 opacity-70 hover:opacity-100"
      >
        <FaAngleRight />
      </button>
    </div>
  );
}

export default Pagination;
