import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import axios from "axios";
import {
  FaStar,
  FaSquareFacebook,
  FaSquareXTwitter,
  FaSquareShareNodes,
  FaSquareReddit,
} from "react-icons/fa6";
import Header from "../../Components/Header";
import Footer from "../../Components/Footer";
import LoadingGif from "../../assets/loading.gif";
import "@vidstack/react/player/styles/default/theme.css";
import "@vidstack/react/player/styles/default/layouts/audio.css";
import "@vidstack/react/player/styles/default/layouts/video.css";
import { MediaPlayer, MediaProvider, Poster } from "@vidstack/react";
import {
  DefaultVideoLayout,
  defaultLayoutIcons,
} from "@vidstack/react/player/layouts/default";

function Movie() {
  const { slug } = useParams();
  const [movie, setMovie] = useState(null);
  const [movies, setMovies] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [episodes, setEpisodes] = useState([]);
  const [selectedEpisode, setSelectedEpisode] = useState(null);
  const [movieLoaded, setMovieLoaded] = useState(false);

  useEffect(() => {
    axios
      .get(`https://phimapi.com/phim/${slug}`)
      .then((res) => {
        if (res.data.status) {
          setMovie(res.data.movie);
          const allServerData = res.data.episodes
            .map((ep) => ep.server_data)
            .flat();
          setEpisodes(allServerData);
          setSelectedEpisode(allServerData[0]);
          setMovieLoaded(true);
          console.log(res.data.movie);
          console.log(allServerData);
        } else {
          setError("Phim s c cp nhp sm nht");
        }
        setLoading(false);
      })
      .catch((error) => {
        setError("Phim s c cp nhp sm nht");
        setLoading(false);
      });
  }, [slug]);

  useEffect(() => {
    if (movieLoaded) {
      let type = "phim-moi-cap-nhat";
      if (movie && movie.type === "hoathinh") {
        type = "hoat-hinh";
      } else if (movie && movie.type === "single") {
        type = "phim-le";
      } else if (movie && movie.type === "series") {
        type = "phim-bo";
      } else if (movie && movie.type === "tvshows") {
        type = "tv-shows";
      } else {
        type = "phim-moi-cap-nhat";
      }
      axios
        .get(`https://phimapi.com/v1/api/danh-sach/${type}`)
        .then((res) => {
          if (res.data.status) {
            const limitedMovies = res.data.data.items.slice(0, 6);
            setMovies(limitedMovies);
          } else {
            setError("Failed to fetch movies");
          }
          setLoading(false);
        })
        .catch((error) => {
          setError("Error fetching data");
          setLoading(false);
        });
    }
  }, [movieLoaded, movie]);

  const handleEpisodeClick = (episode) => {
    setSelectedEpisode(episode);
  };

  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://connect.facebook.net/vi_VN/sdk.js#xfbml=1&version=v20.0&appId=367660879695093";
    script.async = true;
    script.defer = true;
    script.crossOrigin = "anonymous";
    script.nonce = "Iv8Zus5H";
    script.onload = () => {
      if (window.FB) {
        window.FB.init({
          appId: "367660879695093",
          autoLogAppEvents: true,
          xfbml: true,
          version: "v20.0",
        });
        window.FB.XFBML.parse();
      }
    };
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  useEffect(() => {
    if (window.FB) {
      window.FB.XFBML.parse();
    }
  }, [movie]);

  return (
    <div className="min-h-screen flex flex-col">
      <div id="fb-root"></div>
      <Header />
      {loading && (
        <div className="flex-grow flex justify-center items-center">
          <img
            src={LoadingGif}
            alt="Loading..."
            className="w-[100px] h-[100px]"
          />
        </div>
      )}
      {error && <div className="flex-grow">{error}</div>}
      {!loading && !error && movie && (
        <div className="my-3 md:mt-6 flex flex-col md:flex-row">
          <div className="w-full md:w-3/12 px-2 md:ml-6 md:px-0">
            <img src={movie.poster_url} alt={movie.name} className="w-full" />
            <div className="mt-3 line-clamp-1 text-2xl font-semibold">
              {movie.name}
            </div>
            <div className="text-light-text mt-1">
              Cp nhp: {movie.modified.time.slice(0, 10)}
            </div>
            <div className="text-light-text mt-1">Thi lng: {movie.time}</div>
            <div className="w-full h-[76px] my-4 bg-medium-blue flex justify-around items-center text-light-text">
              <div className="flex text-2xl">
                <FaStar />
                <FaStar />
                <FaStar />
                <FaStar />
                <FaStar />
              </div>
              <div className="text-xl">../10</div>
              <div className="text-xl">(no review)</div>
            </div>
            <div className="line-clamp-8 text-light-text text-sm">
              {movie.content}
            </div>
            <hr className="my-6 bg-gray-400 opacity-[0.2]" />
            <div className="grid grid-cols-2">
              <div className="text-light-text">Tn y :</div>
              <div className="text-light-text">{movie.name}</div>
              <div className="text-light-text">Th loi:</div>
              <div className="text-light-text">
                {movie.category.map((category) => (
                  <span key={category.id}>{category.name}, </span>
                ))}
              </div>
              <div className="text-light-text">Trng thi:</div>
              <div className="text-light-text">{movie.status}</div>
              <div className="text-light-text">Nm:</div>
              <div className="text-light-text">{movie.year}</div>
              <div className="text-light-text">o din:</div>
              <div className="text-light-text">{movie.director}</div>
              <div className="text-light-text ">Din vin:</div>
              <div className="text-light-text line-clamp-6">{movie.actor}</div>
            </div>
            <hr className="my-6 bg-gray-400 opacity-[0.2]" />
            <div className="mt-6 flex justify-center text-4xl">
              <i className="mr-2 text-[#4165af]">
                <FaSquareFacebook />
              </i>
              <i className="mr-2 text-white">
                <FaSquareXTwitter />
              </i>
              <i className="mr-2 text-[#ff4500]">
                <FaSquareReddit />
              </i>
              <i className="mr-2 text-[#0173f8]">
                <FaSquareShareNodes />
              </i>
            </div>
          </div>
          <div className="w-full md:w-9/12 md:ml-6 md:mr-8 md:px-0 mt-6 md:mt-0 px-2">
            <MediaPlayer
              src={selectedEpisode.link_m3u8}
              viewType="video"
              streamType="on-demand"
              logLevel="warn"
              crossOrigin
              playsInline
              title={movie.name}
              poster={movie.poster_url}
            >
              <MediaProvider>
                <Poster className="vds-poster" />
              </MediaProvider>
              <DefaultVideoLayout
                thumbnails={movie.thumb_url}
                icons={defaultLayoutIcons}
              />
            </MediaPlayer>

            {episodes.length > 1 && (
              <div className="mt-4">
                <div className="text-xl font-semibold mb-2">Cc tp:</div>
                <ul
                  className={`${
                    episodes.length > 30
                      ? "h-96 overflow-y-scroll scrollbar-hide"
                      : ""
                  } grid grid-cols-4 gap-3 xl:grid-cols-10 lg:grid-cols-8 md:grid-cols-6 sm:grid-cols-4`}
                >
                  {episodes.map((episode, index) => (
                    <li
                      key={index}
                      className={`cursor-pointer p-4 flex justify-center items-center text-center bg-medium-blue ${
                        episode === selectedEpisode
                          ? "text-blue-500"
                          : "text-gray-700"
                      }`}
                      onClick={() => handleEpisodeClick(episode)}
                    >
                      Tp {index + 1}
                    </li>
                  ))}
                </ul>
              </div>
            )}

            <div className="mt-12">
              <div className="text-xl font-semibold">C th bn s thch</div>
              <ul className="grid grid-cols-2 gap-y-9 justify-items-center xl:flex xl:justify-around mt-8 xl:ml-0 sm:grid-cols-3">
                {movies.map((movie) => (
                  <li
                    className="w-[150px] xl:h-[210px] relative transition-transform duration-300 ease-in-out hover:scale-110"
                    key={movie._id}
                  >
                    <Link to={`/movie/${movie.slug}`}>
                      <img
                        src={`https://img.phimapi.com/${movie.poster_url}`}
                        alt={movie.name}
                        className="w-[150px] h-[210px] object-cover"
                      />
                      <p className="absolute bottom-0 leading-[42px]  w-full h-[42px] bg-black bg-opacity-70 text-sm font-semibold mt-2 text-center line-clamp-1 xl:mb-0">
                        {movie.name}
                      </p>
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
            <div className="mt-12 text-xl font-semibold">Bnh lun phim</div>
            <div className="bg-white mt-4 w-full">
              <div
                className="fb-comments"
                data-href={`https://duythai03.github.io/free-movie/#/movie/${movie.slug}`}
                data-width="100%"
                data-mobile="autp"
                data-numposts="5"
              ></div>
            </div>
          </div>
        </div>
      )}
      <Footer />
    </div>
  );
}

export default Movie;
